<template>
  <div class="hello">
    <img
      src="@/assets/123.jpg"
      alt=""
      @click="toggleEvent"
    >
    <ml-i-view
      v-model="imgShow"
      :url="imgSrc"
      :scale="10"
    ></ml-i-view>
  </div>
</template>

<script>
const imgSrc = require("@/assets/123.jpg");

export default {
  data() {
    return {
      imgShow: false,
      imgSrc: ""
    };
  },
  created() {
    this.$nextTick(() => {
      this.imgSrc = imgSrc;
    });
  },
  methods: {
    toggleEvent() {
      this.imgShow = !this.imgShow;
      /*  if (this.imgShow) {
        this.imgSrc = "../assets/123.jpg";
      } else {
        this.imgSrc = "";
      } */
    }
  }
};
</script>

<style scoped>
.hello {
  padding: 20px;
}
.hello img {
  width: 100%;
  height: 100%;
}
</style>



